export default {
  props: ['articleMess', 'index'],
  data() {
    return {
      imgclass: ''
    };
  },
  methods: {
    sendIndex() {
      this.$emit('weiboDetailIndex', this.index);
    }
  },
  created() {
    if (this.articleMess.imgtype == 1) {
      this.imgclass = 'img1';
    } else if (this.articleMess.imgtype > 4) {
      this.imgclass = "imgOver4";
    } else {
      this.imgclass = 'imgLess4';
    }
  }
};