import axios from 'axios';
export default {
  data() {
    return {
      email: '',
      password: ''
    };
  },
  methods: {
    closeLoginForm() {
      this.$emit('loginform', false);
    },
    //
    getCode() {
      axios.post('http://jp-tyo-ntt-1.natfrp.cloud:16269/api/email', {
        email: this.email
      }).then(res => {
        console.log(res);
      }, err => {
        console.log(err);
      });
    },
    changePassword() {
      console.log('2');
    },
    toChangePassword() {
      this.$emit('logincomponent', 'forgetPassword');
