// import logoform from './components/logoform.vue'
//
import bus from './utils/eventBus';
//
import loginform from './indexLogin/loginform.vue';
import forgetPassword from './indexLogin/forgetPassword.vue';
import logonAcount from './indexLogin/logonAcount.vue';
//
import userTreaty from './indexLogin/userTreaty.vue';
import privacyPolicy from './indexLogin/privacyPolicy.vue';
import chat from './components/chat.vue';
export default {
  name: 'App',
  comments: {
    chat
  },
  components: {
    loginform,
    forgetPassword,
    logonAcount,
    userTreaty,
    privacyPolicy
  },
  data() {
    return {
      //
      loginstatu: false,
      // loginstatu: true,
      showloginform: false,
      logincomponent: loginform,
      // 
      treatyStatu: false,
      treaty: userTreaty,
      // userMessage: {
      //   userName:'',
      //   userId:"01",
      // userHeadPhotoSrc:require('@/assets/image/head/.jpg'),
      // },
      userMessage: {},
      //
      chatStatu: false,
      //css
      menuBox: 'menuBox',
      loginBtn: 'loginBtn',
      menuBtn: 'menuBtn',
      //header
      nameClass: '',
      headlinebox: 'headlinebox',
      userControlShow: true,
      userControlAClass: 'hvr-underline-from-left white--text',
      //
      loginBtnSrc: require('./assets/icon/login-circle-line.svg'),
      menuBtnSrc: require('./assets/icon/menu-fold-line.svg')
    };
  },
  methods: {
    //menu
    menulogoswich() {
      if (this.menuBox == 'menuBox') {
        this.menuBox = 'menuBoxactive';
        this.menuBtnSrc = require('./assets/icon/close-circle-line.svg');
      } else {
        this.menuBox = 'menuBox';
        this.menuBtnSrc = require('./assets/icon/menu-fold-line.svg');
      }
      if (window.location.hash == '#/index') {
        this.userControlAClass = 'hvr-underline-from-left white--text';
        this.nameClass = '';
        this.menuBtn = 'menuBtn';
        this.loginBtn = 'loginBtn';
        this.headlinebox = 'headlinebox';
      } else {
        this.userControlAClass = 'hvr-underline-from-left black--text';
        this.nameClass = 'black--text';
        this.menuBtn = 'menuBtn menuBtnActive';
        this.headlinebox = 'headlinebox headlineboxActive';
        if (this.loginstatu == false) {
          this.loginBtn = 'loginBtn loginBtnActive';
        } else {
          this.loginBtn = 'loginBtn';
        }
      }
    },
    //
    userLogin() {
      if (this.loginstatu == false) {
        if (this.showloginform == false) {
          this.showloginform = true;
        } else {
          this.showloginform = false;
        }
      } else {
        if (this.userControlShow == true) {
          this.userControlShow = false;
        } else {
          this.userControlShow = true;
        }
      }
    },
    //
    clockLoginForm() {
      this.showloginform = false;
    },
    //// 
    changlogincomponent(val) {
      this.logincomponent = val;
      console.log(val);
    },
    //
    changeLoginStatu(val) {
      console.log(val);
      this.loginstatu = val.loginstatu;
      this.userMessage = {
        userName: val.userName,
        userId: val.userId,
        userHeadPhotoSrc: require('@/assets/image/head/' + val.userHeadPhotoSrc)
      };
      //
      this.showloginform = false;
    },
    openTreaty(val) {
      this.treatyStatu = true;
      this.treaty = val;
    },
    closeTreaty() {
      this.treatyStatu = false;
    },
    closeChat() {
      this.chatStatu = false;
    }
  },
  created() {
    this.$message({
      showClose: true,
      message: '',
      iconClass: "el-icon-user"
    });
    bus.$on("chatmess", val => {
      this.chatStatu = true;
    });
  }
};