import "core-js/modules/es.array.push.js";
import equipment from '@/components/equipment.vue';
import ActionBar from '@/components/ActionBar.vue';
import equipmentDetail from '@/components/equipmentDetail.vue';
export default {
  comments: {
    equipment,
    ActionBar,
    equipmentDetail
  },
  data() {
    return {
      isCollapse: true,
      //
      detailStatu: false,
      equipment: {},
      //
      payOrder: []
    };
  },
  methods: {
    changeDrawer() {
      this.drawer = !this.drawer;
      if (this.drawer) {
        this.drawerButton = '<';
      }
    },
    //
    tobox(e) {
      console.log(this);
      const id = 'box' + e.$props.index;
      // console.log(id)
      var anchor = document.getElementById(id);
      // chrome,main
      this.$refs.mainBox.scrollTop = anchor.offsetTop;
    },
    closeDetail(val) {
      this.detailStatu = false;
    },
    openDetail() {
      this.detailStatu = true;
    },
    //
    priduceMess(val) {
      const result = this.findpayindex(val.index);
      // console.log('result',result)
      if (result != -1) {
        this.payOrder[result].num = val.num;
      } else {
        this.payOrder.push(val);
      }
      // console.log(this.payOrder)
    },

    deletePriduce(index) {
      const i = this.findpayindex(index);
      this.payOrder.splice(i, 1);
    },
    //
    //
    findpayindex(val) {
      // console.log('val',val)
      for (var i = 0; i < this.payOrder.length; i++) {
        // console.log(val.index,this.payOrder[i].index)
        if (val == this.payOrder[i].index) {
          return i;
        }
      }
      return -1;
    }
  },
  created() {
    if (document.body.clientWidth > 500) {
      this.leftSideWidth = '100px';
    } else {
      this.leftSideWidth = '15vw';
    }
    this.equipment = {
      single: {
        foxi: [{
          name: '1'
        }]
      }
    };
  }
};