export default {
  props: ['payOrder'],
  data() {
    return {
      count: 0,
      money: 0
    };
  },
  created() {},
  methods: {
    gopayOrder() {
      const h = this.$createElement;
      this.$notify({
        title: '',
        message: h('i', {
          style: 'color: teal'
        }, `${this.money}:l15980026007`)
      });
    }
  },
  watch: {
    payOrder: {
      handler(newval) {
        // console.log(newval)
        this.count = newval.length;
        let totleprice = 0;
        for (var i = 0; i < newval.length; i++) {
          totleprice = totleprice + newval[i].num * newval[i].price;
        }
        this.money = totleprice;
      },
      deep: true,
      immediate: true
    }
  }
};