import "core-js/modules/es.array.push.js";
export default {
  data() {
    return {
      imageUrl: []
    };
  },
  methods: {
    closeWrite() {
      this.$emit('closePage', false);
    },
    submitUserWirte() {
      this.$message({
        message: '',
        type: 'success'
      });
      this.closeWrite();
    },
    change(e) {
      console.log(e.target.files);
      // 
      // let name  =  e.target.files[0].name

      // 
      let file = e.target.files;
      for (var i = 0; i < file.length; i++) {
        var reader = new FileReader();
        var that = this;
        reader.readAsDataURL(file[i]);

        // 
        reader.onload = e => {
          console.log('');

          // e.target.result    DataURL
          if (that.imageUrl.length < 9) {
            that.imageUrl.push(e.target.result);
          } else {
            this.$message({
              message: '9',
              iconClass: 'el-icon-s-promotion',
              type: 'warning'
            });
          }

          // 
        };
      }
    }
  },

  created() {}
};