// import { url } from 'inspector';

export default {
  props: ["index"],
  data() {
    return {
      price: Math.floor(Math.random() * 500 + 50),
      num: 1,
      title: '',
      descibe: '',
      thumb: require('@/assets/image/equipment/.jpg'),
      statu: false
    };
  },
  methods: {
    handleChange(value) {
      console.log(value);
    },
    openDetail() {
      this.$emit('openDetail', true);
    }
  },
  watch: {
    statu: {
      handler(newval, oldval) {
        if (newval == false) {
          this.num = 1;
          this.$emit('deletePriduce', this.index);
        } else {
          const priduceMess = {
            num: this.num,
            price: this.price,
            index: this.index
          };
          this.$emit('priduceMess', priduceMess);
        }
      },
      immediete: true
    },
    num: {
      handler: function (newval, oldval) {
        if (this.statu) {
          const priduceMess = {
            num: this.num,
            price: this.price,
            index: this.index
          };
          this.$emit('priduceMess', priduceMess);
        }
      },
      immediete: true
    }
  }
};